import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)
const store = new Vuex.Store({
  state:{
      name:'',
      url:'',
      tabList:[]
    },
  getters:{
    getName(state){
      if(state.name){
        return state.name
      }else{
        return sessionStorage.getItem('name')
      }

    },
    getTab(state){
      console.log('getTab',state.tabList)

      if(state.tabList[0]){
        return state.tabList
      }else{
        return JSON.parse(sessionStorage.getItem('tab'))
      }
    },
    getUrl(state){
      if(state.url){
        return state.url
      }else{
        return JSON.parse(sessionStorage.getItem('url'))
      }
    }
  },
  actions:{

  },
  mutations:{
    menu(state,name){
      sessionStorage.setItem('name',name)
      state.name = name
    },
    tabList(state,url){
     console.log('urlllllll',url)
      // state.tabList = ['fileReview','fileIndicatorDisk']
      state.tabList.push('fileReview')
      sessionStorage.setItem('tab',JSON.stringify(['fileReview','fileIndicatorDisk']))
    }
  }
})
export default store



