<template>
  <div>

    <div class="ll">
    <object style="opacity: 0;position:relative;z-index: -10000;" refs="ScanSDK_OCX" name='x' CLASSID='CLSID:25540CEC-FCB7-42B4-B636-3533F2ED7868' width="100" height="30" align=center hspace=0 vspace=0></object>

    <input type='button' value='扫描控制' @click='CallScan()'/>
    <input type='button' value='扫描默认' @click='CallScanM()'/>

    <input type='button' value='显示ScanSaveDir属性的值' @click='ShowScanSaveDir()'/>
    <input type='button' value='设置ScanSaveDir属性的值' @click='SetScanSaveDir()'/>

    <input type='button' value='获取文件列表' @click='CallGetScanFileName()'/>

    <br>
    <br>
    <input type='button' value='调用GetFileBase64方法' @click='CallShowMsg()'/>
    <input type='button' value='调用GetCurrScannerInfo方法' @click='CallCurrScannerInfo()'/>
    <br>
    <br>
    <!--<button @click="base()">base64码转图片</button>-->
    <!--<button @click="baseFile()">传file文件</button>-->
  </div>
  </div>
</template>
<SCRIPT type="text/javascript" FOR="ScanSDK_OCX" EVENT="OnScanOnePage(nScanIndex, nRet)" >
  var AScan = document.getElementById('ScanSDK_OCX')
  // var AScan = this.$refs["ScanSDK_OCX"].target

  // OnScanOnePage: 扫描单张触发事件
  // nScanIndex: 本次扫描索引(每次扫描都从1开始累加, 主要用于连续扫描计数)
  // AScan.GetCurrScanFileName(): 获取当前扫描文件路径
  // nRet: 0.失败, 1.成功
  alert('OnScanOnePage - nScanIndex: ' + '' + ', nRet: ' + '' + ' , FileName: ' + AScan.GetCurrScanFileName())

</SCRIPT>

<script>
  export default {
    data(){
      return {
        modeInfo:"",
        Loading:false,
        type:false,
