        typesOf:'',
        list:[],
      }
    },
    mounted () {
      this.initDevice()
    },
    methods: {
      initDevice () {	// 初始化配置
        // alert("initDevice");
        var AScan = document.getElementById('ScanSDK_OCX')
        // var AScan = this.$refs["ScanSDK_OCX"].target
        // 设置扫描目录
        AScan.ScanSaveDir = 'C:\\Users\\13941\\Downloads'
        // 设置扫描文件名前缀
        AScan.Prefix = 'scan_image_'
        // 扫描文件起始流水号
        AScan.StartSN = '5'
        // 流水号长度
        AScan.SNLen = 3
        // 扫描图像格式: 0.bmp, 1.jpg
        AScan.ScanImageFormat = 100
        // Jpg压缩比例(1 -- 100, 仅jpg有效)
        AScan.ImageCompress = 1
        // AScan.Visible = false;
        // 扫描模式: 0.不显示TWAIN窗口, 1. 显示TWAIN窗口
        AScan.ScanMode = 1
        // 颜色扫描模式(1.黑白(正面), 2.黑白(背面), 4.灰度(正面), 8.灰度(背面), 16.彩色(正面) 32.彩色(背面))
        // 颜色可以累加, 比如 黑白正/背面: 1+2=3, 灰度正/背面为: 4+8=12, 最多支持3种颜色正背面: 1+2+4+...+32, 即一次生成6张影像.
        // 注意: 平板只支持正面扫描
        // 值该值为 0 则扫描仪当前的设置
        AScan.ScanColorMode = 1
        // 扫描分辨率 (值为0则使用扫描仪的当前dpi)
        AScan.ScanDPI = 350
        // 设置默认扫描仪型号(ScannerName值不为空则, 扫描就不弹出 扫描仪选择窗口)
        AScan.ScannerName = 'AT1340'
      },
      initDeviceM () {	// 默认配置
        // alert("initDevice");
        var AScan = document.getElementById('ScanSDK_OCX')
        // var AScan = this.$refs["ScanSDK_OCX"].target
        // 设置扫描目录
        AScan.ScanSaveDir = 'C:\\Users\\13941\\Downloads'
        // 设置扫描文件名前缀
