        // AScan.Visible = false;
        // 扫描模式: 0.不显示TWAIN窗口, 1. 显示TWAIN窗口
        AScan.ScanMode = 0
        // 颜色扫描模式(1.黑白(正面), 2.黑白(背面), 4.灰度(正面), 8.灰度(背面), 16.彩色(正面) 32.彩色(背面))
        // 颜色可以累加, 比如 黑白正/背面: 1+2=3, 灰度正/背面为: 4+8=12, 最多支持3种颜色正背面: 1+2+4+...+32, 即一次生成6张影像.
        // 注意: 平板只支持正面扫描
        // 值该值为 0 则扫描仪当前的设置
        AScan.ScanColorMode = 1
        // 扫描分辨率 (值为0则使用扫描仪的当前dpi)
        AScan.ScanDPI = 300
        // 设置默认扫描仪型号(ScannerName值不为空则, 扫描就不弹出 扫描仪选择窗口)
        AScan.ScannerName = 'AT1340'
      },

      // 函数
      ShowScanSaveDir () {
        // 显示扫描路径
        alert(x.ScanSaveDir)
      },

      // 函数
      SetScanSaveDir () {
        // 设置扫描路径
        x.ScanSaveDir = 'C:\\Users\\13941\\Downloads'
      },

      CallShowMsg () {
        // x.ShowMsg('Js_CallShowMsg');
        // 获取本地文件的base64码
        // alert(x.GetFileBase64(AScan.GetScanFileName(0)));
        alert(x.GetFileBase64('C:\\test.jpg'))
      },

      CallCurrScannerInfo () {
        // 获取当前扫描仪信息
        alert(x.GetCurrScannerInfo())
      },

      CallScan () {
        // 扫描
        console.log('[[[[[[',x)
        x.Scan()
        // this.list = this.CallGetScanFileName ();
        this.CallGetScanFileName ();
      },

      CallScanM () {
        // 扫描
        this.initDeviceM();
        console.log('[[[[[[',x)
        x.Scan()
        // this.list = this.CallGetScanFileName ();
        this.CallGetScanFileName ();
      },

      CallGetScanFileName () {
        // 获取扫描文件列表
        var list = [];
        // alert('ScanCount: ' + x.ScanCount)

        // ScanCount: 扫描过的文件总数,
        for (let i = 0; i <= x.ScanCount - 1; i++) {
          // GetScanFileName(i) 获取文件名(含路径).  注: i为索引: 0--ScanCount - 1 之间

          // alert(i + '. FileName: ' + x.GetScanFileName(i))
          list.push(x.GetFileBase64(x.GetScanFileName(i)));
          // alert(list)
          // this.list.push(x.GetScanFileName(i));
          // alert(this.list);
        }
        this.base64ToBlob(list)
        console.log('base64',list)
        // return list
        // 可以使用 ClearScanFiles(0) 清掉列表.
        // x.ClearScanFiles(1); //参数: 0: 只清空列表, 不删除文件. 1: 清空列表且删除列表中的文件
        // x.ClearScanFiles(0);
      },
      baseFile(){
        // var forms = new FormData()
        // this.list.forEach((item,index,array)=>{
        //   forms.append('file',item)
        // })
        // this.$https.post('/api/profile-service/imgSynthesisPDF',forms)
          // if(file.length>10){
          //     this.$message({
          //         message: '最多上传10文件',
          //         type: 'warning'
          //     });
          //
          // }else{
          let formData = new FormData();
          for(let i=0;i<this.list.length;i++){
            formData.append('file',this.list[i]);
            // if(file.target.files[i].size/1024 > 10240){
            //     this.$message({
            //         message: '单个文件上传不能超过10M',
            //         type: 'warning'
            //     });
            //     document.getElementById("file").value = ""
            //     return;
            // }
          }
          this.Loading = true
          formData.append('employeeNumber',this.mode.employeeNumber);
          // formData.append('operationNumber',this.getUserInfo.employeeNumber);
          formData.append('archiveMaterialId',item.id);
          formData.append('materialCode',item.materialCode);
          // formData.append('id','1');
          formData.append('materialName',item.materialName);
          formData.append('isRequiredForShow',item.isRequiredForShow);

          this.$https.post('/profile-service/api/uploadMaterial',formData)
            .then((res)=>{
              if(res.data.code == 200){
                this.getFileInfo()
                this.$message({
                  message: '上传成功',
                  type: 'success'
                });
              }else{
                this.$message({
                  message: res.data.msg,
                  type: 'warning'
                });
                this.Loading = false
              }
              document.getElementById("file").value = ""
            })
            .catch((error)=>{
              this.Loading = false
              console.log(error)
            })

      },
      // base(){
      //   this.base64ToBlob()
      // },
      base64ToBlob(dataURI) {
        this.list = []
        dataURI.forEach((item,index)=>{
          let binary = atob(item);
          let array = [];
          for(let i = 0; i < binary.length; i++) {
            array.push(binary.charCodeAt(i));
          }
          var blobMode = new Blob([new Uint8Array(array)], {type:'image/png' });
          // new File([fileData], new Date()+'.jpg');
          this.list.push(blobMode)
        })
        console.log('shuju上路据',typeof this.list)
        this.baseFile()
        // console.log('file文件',new File([files]))
        //   console.log('文件0',list)
        //   this.list = []
        //   list.forEach((item,index,array)=>{
        //   // var arr = item.split(','),
        //   //   mime = arr[0].match(/:(.*?);/)[1],
        //     bstr = atob(item),
        //     n = bstr.length
        //     u8arr = new Uint8Array(n);
        //   while (n--) {
        //     u8arr[n] = bstr.charCodeAt(n);
        //   }
        //   var files = new File([u8arr], '1.jpg', {
        //     type: mime
        //   })
        //
        //   this.list.push(files)
        // })
        console.log('文件1',this.list)
      },

    }
  }
</script>
<style>
  .ll{
    position: relative;
    background:#f00;
  }
</style>



